import {ascending, range, extent, quantile} from 'd3-array';
import {scaleLinear} from 'd3-scale';

import createChart from '../core/chart';
import accessor from '../utils/accessor';
import constant from '../utils/constant';
import {lineDrawing} from './line';

//
//  Box Chart
//  =============
//
//  A box-and-whisker plot uses simple glyphs that summarize a quantitative
//  distribution with five standard statistics: the smallest value, lower
//  quartile, median, upper quartile, and largest value.
//  This summary approach allows the viewer to easily recognize
//  differences between distributions.
//
export default createChart('boxchart', lineDrawing, {

    options: {
        orientation: 'vertical',
        lineWidth: 1,
        x: 'x',
        y: 'y',
        scaleX: 'linear',
        scaleY: 'linear',
        // area with vertical gradient to zero opacity
        gradient: true
    },

    doDraw (frame) {
        var self = this,
            model = this.getModel(),
            //color = this.getModel('color'),
            info = self.getDataInfo(frame),
            box = this.boundingBox(),
            chart = boxplot(),
            paper = this.paper().size(box),
            x = accessor(model.x),
            //y = accessor(model.y),
            boxes = paper.group()
                .attr("transform", this.translate(box.total.left, box.total.top))
                .selectAll('.box')
                .data(info.data),
            //fill = this.fill(info.meta),
            groups = frame.groupby(model.x),
            sx = this.getScale(model.scaleX)
                .domain(extent(frame.data, x)),
            //sy = this.getScale(model.scaleY)
            //    .domain(extent(frame.data, y)),
            width = box.innerWidth;

        if (model.orientation === 'vertical') {
            sx.range([0, box.innerWidth]);
        } else {
            sx.range([0, box.innerHeight]);
            width = this.innerHeight;
        }
        chart.width(this.dim(model.width, width, width/groups.length - 2));

        boxes
            .enter()
                .append('g')
                    .classed('box', true)
                    .call(chart);
    }
});


function boxplot () {
    var width = 1,
        height = 1,
        duration = 0,
        domain = null,
        value = Number,
        whiskers = boxWhiskers,
        quartiles = boxQuartiles,
        tickFormat = null;

    // For each small multiple
    function box (g) {
        g.each((d, i) => {
            d = d.map(value).sort(ascending);

            const
                n = d.length,
                min = d[0],
                max = d[n - 1],
                // Compute quartiles. Must return exactly 3 elements.
                quartileData = d.quartiles = quartiles(d),
                // Compute whiskers. Must return exactly 2 elements, or null.
                whiskerIndices = whiskers && whiskers(d),
                whiskerData = whiskerIndices && whiskerIndices.map(i => d[i]),
                // Compute outliers. If no whiskers are specified, all data are "outliers".
                // We compute the outliers as indices, so that we can join across transitions!
                outlierIndices = whiskerIndices
                    ? range(0, whiskerIndices[0]).concat(range(whiskerIndices[1] + 1, n))
                    : range(n),
                // Compute the new x-scale.
                x1 = scaleLinear()
                    .domain(domain && domain.call(this, d, i) || [min, max])
                    .range([height, 0]),
                // Retrieve the old x-scale, if this is an update.
                x0 = this.__chart__ || scaleLinear()
                  .domain([0, Infinity])
                  .range(x1.range());

            // Stash the new scale.
            this.__chart__ = x1;
            // Note: the box, median, and box tick elements are fixed in number,
            // so we only have to handle enter and update. In contrast, the outliers
            // and other elements are variable, so we need to exit them! Variable
            // elements also fade in and out.

            // Update center line: the vertical line spanning the whiskers.
            var center = g.selectAll("line.center")
              .data(whiskerData ? [whiskerData] : []);

            center.enter().insert("line", "rect")
              .attr("class", "center")
              .attr("x1", width / 2)
              .attr("y1", function(d) { return x0(d[0]); })
              .attr("x2", width / 2)
              .attr("y2", function(d) { return x0(d[1]); })
              .style("opacity", 1e-6)
            .transition()
              .duration(duration)
              .style("opacity", 1)
              .attr("y1", function(d) { return x1(d[0]); })
              .attr("y2", function(d) { return x1(d[1]); });

            center.transition()
              .duration(duration)
              .style("opacity", 1)
              .attr("y1", function(d) { return x1(d[0]); })
              .attr("y2", function(d) { return x1(d[1]); });

            center.exit().transition()
              .duration(duration)
              .style("opacity", 1e-6)
              .attr("y1", function(d) { return x1(d[0]); })
              .attr("y2", function(d) { return x1(d[1]); })
              .remove();

            // Update innerquartile box.
            var box = g.selectAll("rect.box")
              .data([quartileData]);

            box.enter().append("rect")
              .attr("class", "box")
              .attr("x", 0)
              .attr("y", function(d) { return x0(d[2]); })
              .attr("width", width)
              .attr("height", function(d) { return x0(d[0]) - x0(d[2]); })
            .transition()
              .duration(duration)
              .attr("y", function(d) { return x1(d[2]); })
              .attr("height", function(d) { return x1(d[0]) - x1(d[2]); });

            box.transition()
              .duration(duration)
              .attr("y", function(d) { return x1(d[2]); })
              .attr("height", function(d) { return x1(d[0]) - x1(d[2]); });

            // Update median line.
            var medianLine = g.selectAll("line.median")
              .data([quartileData[1]]);

            medianLine.enter().append("line")
              .attr("class", "median")
              .attr("x1", 0)
              .attr("y1", x0)
              .attr("x2", width)
              .attr("y2", x0)
            .transition()
              .duration(duration)
              .attr("y1", x1)
              .attr("y2", x1);

            medianLine.transition()
              .duration(duration)
              .attr("y1", x1)
              .attr("y2", x1);

            // Update whiskers.
            var whisker = g.selectAll("line.whisker")
              .data(whiskerData || []);

            whisker.enter().insert("line", "circle, text")
              .attr("class", "whisker")
              .attr("x1", 0)
              .attr("y1", x0)
              .attr("x2", width)
              .attr("y2", x0)
              .style("opacity", 1e-6)
            .transition()
              .duration(duration)
              .attr("y1", x1)
              .attr("y2", x1)
              .style("opacity", 1);

            whisker.transition()
              .duration(duration)
              .attr("y1", x1)
              .attr("y2", x1)
              .style("opacity", 1);

            whisker.exit().transition()
              .duration(duration)
              .attr("y1", x1)
              .attr("y2", x1)
              .style("opacity", 1e-6)
              .remove();

            // Update outliers.
            var outlier = g.selectAll("circle.outlier")
              .data(outlierIndices, Number);

            outlier.enter().insert("circle", "text")
              .attr("class", "outlier")
              .attr("r", 5)
              .attr("cx", width / 2)
              .attr("cy", function(i) { return x0(d[i]); })
              .style("opacity", 1e-6)
            .transition()
              .duration(duration)
              .attr("cy", function(i) { return x1(d[i]); })
              .style("opacity", 1);

            outlier.transition()
              .duration(duration)
              .attr("cy", function(i) { return x1(d[i]); })
              .style("opacity", 1);

            outlier.exit().transition()
              .duration(duration)
              .attr("cy", function(i) { return x1(d[i]); })
              .style("opacity", 1e-6)
              .remove();

            // Compute the tick format.
            var format = tickFormat || x1.tickFormat(8);

            // Update box ticks.
            var boxTick = g.selectAll("text.box")
              .data(quartileData);

            boxTick.enter().append("text")
              .attr("class", "box")
              .attr("dy", ".3em")
              .attr("dx", (d, i) => i & 1 ? 6 : -6)
              .attr("x", (d, i) => i & 1 ? width : 0)
              .attr("y", x0)
              .attr("text-anchor", function(d, i) { return i & 1 ? "start" : "end"; })
              .text(format)
            .transition()
              .duration(duration)
              .attr("y", x1);

            boxTick.transition()
              .duration(duration)
              .text(format)
              .attr("y", x1);

            // Update whisker ticks. These are handled separately from the box
            // ticks because they may or may not exist, and we want don't want
            // to join box ticks pre-transition with whisker ticks post-.
            var whiskerTick = g.selectAll("text.whisker")
              .data(whiskerData || []);

            whiskerTick.enter().append("text")
              .attr("class", "whisker")
              .attr("dy", ".3em")
              .attr("dx", 6)
              .attr("x", width)
              .attr("y", x0)
              .text(format)
              .style("opacity", 1e-6)
            .transition()
              .duration(duration)
              .attr("y", x1)
              .style("opacity", 1);

            whiskerTick.transition()
              .duration(duration)
              .text(format)
              .attr("y", x1)
              .style("opacity", 1);

            whiskerTick
                .exit()
                .transition()
                .duration(duration)
                .attr("y", x1)
                .style("opacity", 1e-6)
                .remove();
        });
    }

    box.width = function(x) {
        if (!arguments.length) return width;
        width = x;
        return box;
    };

    box.height = function(x) {
        if (!arguments.length) return height;
        height = x;
        return box;
    };

    box.tickFormat = function(x) {
        if (!arguments.length) return tickFormat;
        tickFormat = x;
        return box;
    };

    box.duration = function(x) {
        if (!arguments.length) return duration;
        duration = x;
        return box;
    };

    box.domain = function(x) {
        if (!arguments.length) return domain;
        domain = x === null ? x : constant(x);
        return box;
    };

    box.value = function(x) {
        if (!arguments.length) return value;
        value = x;
        return box;
    };

    box.whiskers = function(x) {
        if (!arguments.length) return whiskers;
        whiskers = x;
        return box;
    };

    box.quartiles = function(x) {
        if (!arguments.length) return quartiles;
        quartiles = x;
        return box;
    };

    return box;
}


function boxWhiskers (d) {
    return [0, d.length - 1];
}


function boxQuartiles (d) {
    return [
        quantile(d, 0.25),
        quantile(d, 0.5),
        quantile(d, 0.75)
    ];
}
